// import { ReadMsgBody, ReadMsgSetParameters, ReadParameters } from "../types/message";
import { utils } from '../utils/index';
import { MessagePriority } from '../types/message';
// TODO: ackId mId 
export interface ReadMsgSetParameters {
	/** The message ID. */
	/**  ID */
	id: string;
	/** The recipient. */
	/**  */
	to: string;
	/** The sender, which can only be the current user and can not be changed. */
	/** */
	from?: string;
	/** The session type. */
	/**  */
	chatType: 'singleChat' | 'groupChat';
}
export interface ReadMsgBody extends ReadMsgSetParameters {
	/** The message type. */
	/**  */
	type: 'read';
	/** Session type. */
	/**  */
	chatType: 'singleChat' | 'groupChat';
	/** The ID of the read message */
	/**  ID */
	ackId: string;
	/** The ID of the read message This parameter has the same value as ackId.*/
	/**  ID ackId */
	mid?: string;
	/** The number of group members that have read the messages. */
	/**  */
	groupReadCount?: { [key: string]: number };
	/** The message content. */
	/**  */
	ackContent?: string;
	/** Whether the message is a thread message. */
	/**  */
	isChatThread?: boolean;
	/** Message priority. */
	/**  */
	priority?: MessagePriority;
	/** Whether the message is delivered only when the recipient(s) is/are online:
	 *  - `true`: The message is delivered only when the recipient(s) is/are online. If the recipient is offline, the message is discarded.
	 *  - (Default) `false`: The message is delivered when the recipient(s) is/are online. If the recipient(s) is/are offline, the message will not be delivered to them until they get online.
	 */
	/** 
	 * - `true`: 
	 * - `false`: 
	 */
	deliverOnlineOnly?: boolean;
}
export interface ReadParameters {
	/** The message type. */
	/**  */
	type: 'read';
	/** The recipient. */
	/**  */
	id: string;
}

export interface CreateReadMsgParameters {
	/** The recipient. */
	/**  */
	to: string;
	/** The sender, which can only be the current user and can not be changed. */
	/** */
	from?: string;
	/** The message type. */
	/**  */
	type: 'read';
	/** The session type. */
	/**  */
	chatType: 'singleChat' | 'groupChat';
	/** The ID of the read message. */
	/**  ID */
	id: string;
	/** The message content. */
	/**  */
	ackContent?: string;
	/** Whether the message is a thread message. */
	/**  */
	isChatThread?: boolean;
	/** Message priority. */
	/**  */
	priority?: MessagePriority;
	/** Whether the message is delivered only when the recipient(s) is/are online:
	 *  - `true`: The message is delivered only when the recipient(s) is/are online. If the recipient is offline, the message is discarded.
	 *  - (Default) `false`: The message is delivered when the recipient(s) is/are online. If the recipient(s) is/are offline, the message will not be delivered to them until they get online.
	 */
	/** 
	 * - `true`: 
	 * - `false`: 
	 */
	deliverOnlineOnly?: boolean;
}

export class Read {
	id: string;
	type: 'read';
	body?: ReadMsgBody;
	constructor(parameters: ReadParameters) {
		const id = parameters.id;
		this.id = id;
		this.type = parameters.type;
	}
	/**
	 * @hidden
	 */
	set(options: ReadMsgSetParameters) {
		this.body = {
			id: this.id,
			ackId: options.id,
			type: 'read',
			to: options.to,
			from: options.from || '',
			chatType: options.chatType,
		};
	}
	static create(options: CreateReadMsgParameters): ReadMsgBody {
		return {
			id: utils.getUniqueId(),
			chatType: options.chatType,
			ackId: options.id,
			type: 'read',
			to: options.to,
			from: options.from || '',
			ackContent: options.ackContent,
			isChatThread: options.isChatThread,
			priority: options.priority,
			deliverOnlineOnly: options.deliverOnlineOnly,
		};
	}
}

