import { MessageBody } from '../types/message';
import { LocalMessageBody, ConversationType } from '../types/localCacheApi';

type LocalMessage<Item extends MessageBody> = Item extends MessageBody
	? Item & {
			sessionId: string;
			serverMsgId?: string;
			status: MessageStatus;
			isChatThread: boolean;
	  }
	: never;

type LocalMessageType = LocalMessage<LocalMessageBody>;

enum MessageStatus {
	CREATE,
	FAIL,
	INPROGRESS,
	SUCCESS,
}

interface MessageUpdateOptions {
	status?: MessageStatus;
	serverMsgId?: string;
}

interface LocalConversation {
	conversationId: string; // IDID
	conversationType: ConversationType; // 
	unReadCount?: number; // 
	lastMessageId?: string; // ID
	lastMessageFromOtherId?: string; // ID
	customField?: Record<string, any>; // 
	updateTime?: number; // 
	sessionId: string; // `${ConversationType}-${conversationId}
	unreadCountClearTimestamp?: number; // 
}

type CacheConversationMap = Record<string, LocalConversation>;

type ConversationUpdateInfo = Pick<
	LocalConversation,
	| 'customField'
	| 'lastMessageFromOtherId'
	| 'lastMessageId'
	| 'updateTime'
	| 'unReadCount'
	| 'unreadCountClearTimestamp'
>;

export type {
	LocalMessageType,
	MessageUpdateOptions,
	ConversationType,
	ConversationUpdateInfo,
	LocalConversation,
	LocalMessageBody,
	CacheConversationMap,
};
export { MessageStatus };
