/**
 * Set the DND of the session.
 *
 * ```typescript
 * connection.setSilentModeForConversation({conversationId: '100', type: 'singleChat', params: {paramType: 0, remindType: 'ALL'}})
 * ```
 */

/**
 * 
 *
 * ```typescript
 * connection.setSilentModeForConversation({conversationId: '100', type: 'singleChat', params: {paramType: 0, remindType: 'ALL'}})
 * ```
 */
function setSilentModeForConversation(
	this: Connection,
	params: {
		/**
		 * The Conversation id.
		 * For one-to-one chat, conversation ID is to chat user's name.
		 * For group chat, conversation ID is group ID, different with group name.
		 * For chat room, conversation ID is chat room ID, different with chat room name.
		 * For help desk, it is same with one-to-one chat, conversation ID is also chat user's name.
		 */
		/**
		 *  id
		 *  ID 
		 *  ID  ID
		 *  ID  ID
		 * Help Desk  ID 
		 */
		conversationId: string;
		/** Conversation type. */
		/**  */
		type: SilentModeType.CONVERSATIONTYPE;
		/** Options object include paramType,remindType,duration,startTime,endTime. */
		/** paramTyperemindTypedurationstartTimeendTime*/
		options: SilentModeType.SilentModeParamType;
		success?: (
			res: AsyncResult<SilentModeType.SilentModeConversationType>
		) => void;
		error?: (error: ErrorEvent) => void;
	}
): Promise<AsyncResult<SilentModeType.SilentModeConversationType>> {
	if (typeof params.conversationId !== 'string' || !params.conversationId) {
		throw Error('Invalid parameter: "conversationId"');
	}
	if (typeof params.type !== 'string' || !params.type) {
		throw Error('Invalid parameter: "type"');
	}
	if (!(params.options instanceof Object)) {
		throw Error('Invalid parameter: "options"');
	}
	const { paramType } = params.options;
	if (typeof paramType !== 'number' || paramType < 0 || paramType > 2) {
		throw Error('Invalid parameter: "options of paramType"');
	}
	if (paramType === 0) {
		const { remindType } = params.options;
		if (typeof remindType !== 'string') {
			throw Error('Invalid parameter: "options of remindType"');
		}
	} else if (paramType === 1) {
		const { duration } = params.options;
		if (typeof duration !== 'number') {
			throw Error('Invalid parameter: "options of duration"');
		}
	} else if (paramType === 2) {
		const { startTime, endTime } = params.options;
		if (!(startTime instanceof Object) || !Object.keys(startTime).length) {
			throw Error('Invalid parameter: "options of startTime"');
		}
		if (
			!startTime.hours ||
			typeof startTime.hours !== 'number' ||
			!startTime.minutes ||
			typeof startTime.minutes !== 'number'
		) {
			throw Error(
				'Invalid parameter: "options of startTime of hours or minutes"'
			);
		}
		if (!(endTime instanceof Object) || !Object.keys(endTime).length) {
			throw Error('Invalid parameter: "options of endTime"');
		}
		if (
			!endTime.hours ||
			typeof endTime.hours !== 'number' ||
			!endTime.minutes ||
			typeof endTime.minutes !== 'number'
		) {
			throw Error(
				'Invalid parameter: "options of endTime of hours or minutes"'
			);
		}
	}
	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	const { accessToken, orgName, appName, userId, jid } = this.context;

	let type = 'chatgroup';
	let data = {};
	switch (paramType) {
		case 0:
			const { remindType } = params.options;
			data = {
				type: remindType,
			};
			break;
		case 1:
			const { duration } = params.options;
			data = {
				ignoreDuration: duration,
			};
			break;
		case 2:
			const { startTime, endTime } = params.options;
			data = {
				ignoreInterval: `${startTime.hours}:${startTime.minutes}-${endTime.hours}:${endTime.minutes}`,
			};
			break;
		default:
			break;
	}
	if (params.type === 'singleChat') {
		type = 'user';
	}

	const options = {
		url: `${this.apiUrl}/${orgName}/${appName}/users/${userId}/notification/${type}/${params.conversationId}?resource=${jid.clientResource}`,
		type: 'PUT',
		dataType: 'json',
		data: JSON.stringify(data),
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
		success: params.success,
		error: params.error,
	};
	logger.debug('Call setSilentModeForConversation:', params);
	return ajax.call(this, options);
}

/**
 * Clear the setting of offline push notification type for the session.
 *
 * ```typescript
 * connection.clearRemindTypeForConversation({conversationId: '123', type: 'singleChat'})
 * ```
 */

/**
 * 
 *
 * ```typescript
 * connection.clearRemindTypeForConversation({conversationId: '123', type: 'singleChat'})
 * ```
 */
function clearRemindTypeForConversation(
	this: Connection,
	params: {
		/** Conversation id. */
		/**  id */
		conversationId: string;
		/** Conversation type. */
		/**  */
		type: SilentModeType.CONVERSATIONTYPE;
		success?: (
			res: AsyncResult<SilentModeType.SilentModeConversationType>
		) => void;
		error?: (error: ErrorEvent) => void;
	}
): Promise<AsyncResult<SilentModeType.SilentModeConversationType>> {
	if (typeof params.conversationId !== 'string' || !params.conversationId) {
		throw Error('Invalid parameter: "conversationId"');
	}
	if (typeof params.type !== 'string' || !params.type) {
		throw Error('Invalid parameter: "type"');
	}

	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	const { accessToken, orgName, appName, userId, jid } = this.context;
	let type = 'chatgroup';
	const data = {
		type: 'DEFAULT',
	};
	if (params.type === 'singleChat') {
		type = 'user';
	}
	const options = {
		url: `${this.apiUrl}/${orgName}/${appName}/users/${userId}/notification/${type}/${params.conversationId}?resource=${jid.clientResource}`,
		type: 'PUT',
		dataType: 'json',
		data: JSON.stringify(data),
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
		success: params.success,
		error: params.error,
	};
	logger.debug('Call clearRemindTypeForConversation:', params);
	return ajax.call(this, options);
}

