/**
 * Obtain the DND Settings of specified sessions in batches.
 *
 * ```typescript
 * connection.getSilentModeForConversations({conversationList:[{id: 'userId', type: 'singleChat'}, {id: 'groupId', type: 'groupChat'}]})
 * ```
 */

/**
 * ( 20)
 *
 * ```typescript
 * connection.getSilentModeForConversations({conversationList:[{id: 'userId', type: 'singleChat'}, {id: 'groupId', type: 'groupChat'}]})
 * ```
 */

function getSilentModeForConversations(
	this: Connection,
	params: {
		/** Conversation list. */
		/**  */
		conversationList: SilentModeType.ConversationListType[];
		success?: (
			res: AsyncResult<SilentModeType.ConversationSilentModeType>
		) => void;
		error?: (error: ErrorEvent) => void;
	}
): Promise<AsyncResult<SilentModeType.ConversationSilentModeType>> {
	if (!Array.isArray(params.conversationList)) {
		throw Error('Invalid parameter: "conversationList"');
	}
	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	const { accessToken, orgName, appName, userId } = this.context;
	const users: string[] = [];
	const groups: string[] = [];
	params.conversationList.forEach((item) => {
		if (item.type === 'singleChat') {
			users.push(item.id);
		} else {
			groups.push(item.id);
		}
	});
	const usersId = users.length ? users.join(',') : '';
	const groupsId = groups.length ? groups.join(',') : '';
	const options = {
		url: `${this.apiUrl}/${orgName}/${appName}/users/${userId}/notification?user=${usersId}&group=${groupsId}`,
		type: 'GET',
		dataType: 'json',
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
		success: params.success,
		error: params.error,
	};
	logger.debug('Call getSilentModeForConversations:', params);
	return ajax.call(this, options);
}

