/**
 * Get all conversations in silent mode.
 * @param params
 * @returns
 * ```typescript
 * connection.getAllSilentModeConversations({pageSize: 10})
 * ```
 */

/**
 * 
 * @param params
 * @returns
 * ```typescript
 * connection.getAllSilentModeConversations({pageSize: 10})
 * ```
 */
function getSilentModeRemindTypeConversations(
	this: Connection,
	params: {
		/** The cursor that specifies where to start to get data. If there will be data on the next page, this method will return the value of this field to indicate the position to start to get data of the next page. If it is null, the data of the first page will be fetched. */
		/**  null  */
		cursor?: string;
		/** The number of data entries per page. The default value is 10. */
		/**  10 */
		pageSize: number;
	}
): Promise<
	AsyncResult<SilentModeType.GetSilentModeRemindTypeConversationsResult>
> {
	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	if (typeof params.pageSize !== 'number') {
		throw Error('Invalid parameter: "pageSize"');
	}
	const { accessToken, orgName, appName, userId } = this.context;
	const requestData = {
		limit: params.pageSize || 10,
		cursor: params.cursor,
	};
	if (!params.cursor) {
		delete requestData.cursor;
	}
	const options = {
		url: `${this.apiUrl}/${orgName}/${appName}/users/${userId}/notification/mute/type`,
		type: 'GET',
		dataType: 'json',
		data: requestData,
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
	};
	logger.debug('Call getSilentModeRemindTypeConversations:', params);
	return ajax.call(this, options).then((res) => {
		if (res.data) {
			const data = res.data.map(
				(
					item:
						| { user: string; value: SilentModeType.SILENTMODETYPE }
						| {
								group: string;
								value: SilentModeType.SILENTMODETYPE;
						  }
				) => {
					if ('user' in item) {
						return {
							conversationId: item.user,
							type: 'singleChat',
							remindType: item.value,
						};
					} else {
						return {
							conversationId: item.group,
							type: 'groupChat',
							remindType: item.value,
						};
					}
				}
			);
			return {
				data: {
					conversations: data,
					cursor: res.cursor,
				},
				type: res.type,
			};
		}
		return res;
	});
}

export {
	setSilentModeForAll,
	getSilentModeForAll,
	setSilentModeForConversation,
	clearRemindTypeForConversation,
	getSilentModeForConversation,
	getSilentModeForConversations,
	setPushPerformLanguage,
	getPushPerformLanguage,
	getSilentModeRemindTypeConversations,
};
