/**
 * Sets custom attributes of a group member.
 * After custom attributes of a group member are set, other members in the group receive the `operation: 'memberAttributesUpdate'`  in the `onGroupEvent` callback and the other devices of the group member receive the  the `operation: 'memberAttributesUpdate'`  in the `onMultiDeviceEvent` callback.
 *
 * ```typescript
 * connection.setGroupMemberAttributes({groupId: 'groupId', userId: 'userId', memberAttributes: {key: 'value'}})
 * ```
 */

/**
 * 
 *  `onGroupEvent`  `memberAttributesUpdate` `onMultiDeviceEvent`  `memberAttributesUpdate`
 *
 * ```typescript
 * connection.setGroupMemberAttributes({groupId: 'groupId', userId: 'userId', memberAttributes: {key: 'value'}})
 * ```
 */

async function setGroupMemberAttributes(
	this: Connection,
	params: {
		/** The group ID. */
		/**  ID */
		groupId: string;
		/** The user ID of the group member. */
		/**  ID */
		userId: string;
		/**
		 * The custom attributes to set in key-value format. In a key-value pair, if the value is set to an empty string, the custom attribute will be deleted.
		 */
		/**
		 *  key-value  key-value  value 
		 */
		memberAttributes: GroupTypes.MemberAttributes;
	}
): Promise<void> {
	const { groupId, userId, memberAttributes } = params;
	if (typeof groupId !== 'string' || groupId === '') {
		throw Error('Invalid parameter: "groupId"');
	}
	if (typeof userId !== 'string' || userId === '') {
		throw Error('Invalid parameter: "userId"');
	}
	if (typeof memberAttributes !== 'object') {
		throw Error('Invalid parameter: "memberAttributes"');
	}
	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	const { orgName, appName, accessToken, jid } = this.context;
	const dt = {
		metaData: memberAttributes,
	};
	const opts = {
		url: `${this.apiUrl}/${orgName}/${appName}/sdk/metadata/chatgroup/${groupId}/user/${userId}?resource=${jid.clientResource}`,
		type: 'PUT',
		dataType: 'json',
		data: JSON.stringify(dt),
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
	};
	logger.debug('Call setGroupMemberAttributes', params);
	await ajax.call(this, opts, OperationName.SET_GROUP_MEMBER_ATTRS);
}

/**
 * Gets all custom attributes of a group member.
 * ```typescript
 * connection.getGroupMemberAttributes({groupId: 'groupId', userId: 'userId'})
 * ```
 */

/**
 * 
 * ```typescript
 * connection.getGroupMemberAttributes({groupId: 'groupId', userId: 'userId'})
 * ```
 */

function getGroupMemberAttributes(
	this: Connection,
	params: {
		/** The group ID. */
		/**  ID */
		groupId: string;
		/** The user ID of the group member. */
		/**  ID */
		userId: string;
	}
): Promise<AsyncResult<GroupTypes.MemberAttributes>> {
	const { groupId, userId } = params;
	return getGroupMembersAttributes
		.call(this, { groupId, userIds: [userId] })
		.then((res) => {
			return {
				type: res.type,
				data: res.data?.[userId],
			};
		});
}

/**
 * Gets custom attributes of multiple group members by attribute key.
 * ```typescript
 * connection.getGroupMembersAttributes({groupId: 'groupId', userIds: ['userId'], keys: ['avatar', 'nickname']})
 * ```
 */

/**
 *  key 
 * ```typescript
 * connection.getGroupMembersAttributes({groupId: 'groupId', userIds: ['userId'], keys: ['avatar', 'nickname']})
 * ```
 */

function getGroupMembersAttributes(
	this: Connection,
	params: {
		/** The group ID. */
		/**  ID */
		groupId: string;
		/** The array of user IDs of group members whose custom attributes are retrieved. */
		/**  ID  */
		userIds: UserId[];
		/** The array of keys of custom attributes to be retrieved. If you pass in an empty array or do not set this parameter, the SDK gets all custom attributes of these group members. */
		/**  key  keys  */
		keys?: string[];
	}
): Promise<AsyncResult<GroupTypes.GetGroupMembersAttributesResult>> {
	const { groupId, userIds, keys = [] } = params;
	if (typeof groupId !== 'string' || groupId === '') {
		throw Error('Invalid parameter: "groupId"');
	}
	if (!Array.isArray(userIds) || userIds?.length <= 0) {
		throw Error('Invalid parameter: "userIds"');
	}
	if (!Array.isArray(keys)) {
		throw Error('Invalid parameter: "keys"');
	}
	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	const { orgName, appName, accessToken } = this.context;
	const dt = {
		targets: userIds,
		properties: keys,
	};
	const opts = {
		url: `${this.apiUrl}/${orgName}/${appName}/sdk/metadata/chatgroup/${groupId}/get`,
		type: 'POST',
		dataType: 'json',
		data: JSON.stringify(dt),
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
	};
	logger.debug('Call getGroupMembersAttributes', params);
	return ajax
		.call(this, opts, OperationName.GET_GROUP_MEMBER_ATTR)
		.then((res) => {
			return {
				type: res.type,
				data: res?.data || {},
			};
		});
}

