import { Code } from '../status';
import ErrorEvent from '../error/error';
import { GroupModifyInfo } from './groupApi';

interface AsyncResult<T = any> {
	/** Status of the request. */
	/**  */
	type: Code;
	/** Data returned by successful request. */
	/**  */
	data?: T;
	/** Data returned by successful request. */
	/**  */
	entities?: T;
	/** message. */
	/**  */
	message?: string;
	[key: string]: any;
}

type UserId = string;
type GroupId = string;
interface CommonRequestResult {
	/** The result of request. */
	/**  */
	result: boolean;
	/** Action. */
	/**  */
	action: string;
	/** The reason of failure. */
	/**  */
	reason?: string;
	/** The user ID. */
	/**  ID */
	user: string;
	/** The group/chat room ID. */
	/** / ID */
	id: string;
}

interface Jid {
	appKey: string;
	clientResource: string;
	domain: string;
	name: string;
}

interface EventData {
	/** The type of operation. */
	/**  */
	operation: string;
	/** The ID of a group or chatroom. */
	/**  ID */
	id: string;
	/** Message sender. */
	/**  */
	from: string;
	/** The modified group info. */
	/**  */
	detail?: GroupModifyInfo;
	/** The name of a group or chatroom.  */
	/**  */
	name?: string;
	/** The userId of a group event.  */
	/**  ID */
	userId?: string;
	/** Custom Attributes.  */
	/**  */
	attributes?: Array<string> | { [key: string]: string };
	/** The member count. For a chat room with more than 2000 members, `memberCount` will not be returned. */
	/**  2000 , `memberCount`   */
	memberCount?: number;
	/** The extension fields. */
	/**  */
	ext?: string;
	/** The reason for the event. */
	/** */
	reason?: string;
	/** The updated announcement. */
	/**  */
	announcement?: string;
}

interface AttributeRespData {
	is_forced: boolean;
	operator: string;
	properties: {
		[key: string]: string;
	};
	result: {
		errorKeys: {
			[key: string]: string;
		};
		successKeys: Array<string>;
	};
	keys?: Array<string>;
	auto_delete?: boolean;
}

export enum OfflineMessagePullState {
	SYNC_INIT = 0,
	SYNC_START,
	SYNC_FINISH,
}

export type {
	AsyncResult,
	UserId,
	CommonRequestResult,
	GroupId,
	ErrorEvent,
	Jid,
	EventData,
	AttributeRespData,
};
