/**
 * Joins the chat room.
 *
 * @note
 * After a user joins successfully, other members in the chat room will receive operation: 'memberPresence' in the `onChatroomEvent` callback.
 *
 * ```typescript
 * connection.joinChatRoom({roomId: 'roomId'})
 * ```
 */

/**
 * 
 *
 * @note
 *  `onChatroomEvent`  `operation:'memberPresence'`
 *
 * ```typescript
 * connection.joinChatRoom({roomId: 'roomId'})
 * ```
 */
function joinChatRoom(
	this: any,
	params: {
		/** The chat room ID. */
		/**  ID*/
		roomId: string;
		/** The reason for joining the chat room. This parameter does not take effect. You can ignore it. */
		/** */
		message?: string;
		/** Extension fields carried when joining a chat room. */
		/** */
		ext?: string;
		/** Whether to leave all the currently joined chat rooms when joining a chat room.
		 * - `true`: The user joins the chat room, while leaving all other chat rooms.
		 * -  (Default) `false`: The user joins the chat room, without leaving all other chat rooms.
		 */
		/** 
		 * - `true`
		 * - `false`
		 */
		leaveOtherRooms?: boolean;
		success?: (res: AsyncResult<ChatRoomTypes.CommonRequestResult>) => void;
		error?: (error: ErrorEvent) => void;
	}
): Promise<AsyncResult<ChatRoomTypes.CommonRequestResult>> {
	const {
		roomId,
		message = '',
		ext = '',
		leaveOtherRooms = false,
		success,
		error,
	} = params;
	if (typeof roomId !== 'string' || roomId === '') {
		throw Error('Invalid parameter roomId');
	}
	if (typeof ext !== 'string') {
		throw Error('Invalid parameter ext');
	}
	if (typeof leaveOtherRooms !== 'boolean') {
		throw Error('Invalid parameter leaveOtherRooms');
	}

	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	logger.debug('Call joinChatRoom', params);
	if (this.logOut) {
		return Promise.reject({
			type: Code.WEBIM_CONNECTION_CLOSED,
			message: 'not login',
		});
	}
	return this.mSync.handleChatRoom(
		{
			roomId,
			ext,
			leaveOtherRooms,
			message,
			success,
			error,
		},
		'join'
	);
}

