/**
 *
 * Gets the list of chat rooms that a user has joined.
 *
 * ```typescript
 * connection.getJoinedChatRooms({pageNum: 1, pageSize: 20})
 * ```
 */

/**
 * 
 *
 * ```typescript
 * connection.getJoinedChatRooms({pageNum: 1, pageSize: 20})
 * ```
 */
function getJoinedChatRooms(
	this: Connection,
	params: {
		/** The page number, starting from 1. */
		/**  1  */
		pageNum: number;
		/** The number of chat rooms to get per page. The value cannot exceed 500. */
		/**  500 */
		pageSize: number;
	}
): Promise<AsyncResult<ChatRoomTypes.JoinedChatRoomsResult>> {
	const { pageNum, pageSize } = params || {};
	if (isNaN(pageNum) || pageNum <= 0) {
		throw Error(`Invalid parameter pageNum:${pageNum}`);
	} else if (isNaN(pageSize) || pageSize <= 0) {
		throw Error(`Invalid parameter pageSize:${pageSize}`);
	}
	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	const requestData = {
		pagenum: pageNum,
		pagesize: pageSize,
		detail: true, // get chat room detail
	};
	const { orgName, appName, accessToken } = this.context;
	const options = {
		url: `${this.apiUrl}/${orgName}/${appName}/users/${this.user}/joined_chatrooms`,
		dataType: 'json',
		type: 'GET',
		data: requestData,
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
	};
	logger.debug('Call getJoinedChatRooms', params);
	return ajax
		.call(this, options, OperationName.GET_USER_JOINED_CHATROOM)
		.then((res) => {
			const dt = (res.data || []) as Array<any>;
			const data = dt.map<ChatRoomTypes.JoinedChatRoomInfo>((item) => {
				const {
					id,
					title: name,
					owner,
					created,
					description,
					max_users: maxusers,
				} = item;
				const info: ChatRoomTypes.JoinedChatRoomInfo = {
					id,
					name,
					owner: owner.split(`${this.appKey}_`)[1], //  appKey_ownerId
					created,
					description,
					maxusers,
				};
				return info;
			});
			return {
				type: res.type,
				data,
			};
		});
}

