/**
 * @hidden
 * @deprecated  {@link getBlocklist} 
 *
 * 
 *
 * ```typescript
 * connection.getBlacklist()
 * ```
 */
function getBlacklist(
	this: any,
	params?: {
		success?: (res: AsyncResult<UserId[]>) => void;
		error?: (error: ErrorEvent) => void;
	}
): Promise<AsyncResult<UserId[]>> {
	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	const { orgName, appName, accessToken } = this.context;

	const suc = (data: any) => {
		const list: { [key: string]: any } = {};
		data.data.forEach((v: string) => {
			list[v] = {
				name: v,
			};
		});
		typeof params?.success === 'function' && params.success(data);
	};
	const error = (res: any) => {
		typeof params?.error === 'function' && params.error(res);
	};
	const opts = {
		url:
			this.apiUrl +
			'/' +
			orgName +
			'/' +
			appName +
			'/users/' +
			this.user +
			'/blocks/users',
		dataType: 'json',
		type: 'GET',
		headers: {
			Authorization: 'Bearer ' + accessToken,
		},
		success: suc,
		error: error,
	};
	logger.debug('Call getBlocklist');
	return ajax.call(this, opts, OperationName.GET_BLACK_LIST);
}

