/**
 * @hidden
 * @deprecated  {@link deleteConversation} 
 *
 * 
 *
 * ```typescript
 * connection.deleteSession()
 * ```
 */
function deleteSession(
	this: any,
	params: {
		/** The conversation ID: The user ID of the peer user or group ID. */
		/**  ID ID  ID*/
		channel: string;
		/**
		 * The conversation type:
		 * - `singleChat`: one-to-one chat;
		 * - `groupChat`: group chat.
		 */
		/**
		 * 
		 * - `singleChat`
		 * - `groupChat`
		 */
		chatType: 'singleChat' | 'groupChat';
		/**
		 * Whether to delete historical messages on the server during conversation deletion.
		 * - `true`: Yes;
		 * - `false`: No.
		 */
		/**
		 * 
		 * - `true`
		 * - `false`
		 */
		deleteRoam: boolean;
		success?: (res: AsyncResult<IndexTypes.DeleteSessionResult>) => void;
		error?: (error: ErrorEvent) => void;
	}
): Promise<AsyncResult<IndexTypes.DeleteSessionResult>> {
	if (params && typeof params.channel !== 'string') {
		throw Error(`Invalid parameter channel: ${params.channel}`);
	}
	if (
		params &&
		params.chatType !== 'singleChat' &&
		params.chatType !== 'groupChat'
	) {
		throw Error(`Invalid parameter chatType: ${params.chatType}`);
	}
	if (params && typeof params.deleteRoam !== 'boolean') {
		throw Error(`Invalid parameter deleteRoam: ${params.deleteRoam}`);
	}
	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	const { orgName, appName, accessToken, jid } = this.context;
	let type: string;
	if (params.chatType === 'singleChat') {
		type = 'chat';
	} else {
		type = 'groupChat';
	}

	const options = {
		url: `${this.apiUrl}/${orgName}/${appName}/user/${this.user}/user_channel?resource=${jid.clientResource}`,
		dataType: 'json',
		type: 'DELETE',
		data: JSON.stringify({
			channel: params.channel,
			type: type,
			delete_roam: params.deleteRoam,
		}),
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
		success: params.success,
		error: params.error,
	};
	logger.debug('Call deleteSession', params);
	return ajax.call(this, options, OperationName.DELETE_SESSION);
}

