/**
 * Queries the user attributes.
 *
 * ```typescript
 * connection.fetchUserInfoById('user1') | fetchUserInfoById(['user1', 'user2'])
 * ```
 */

/**
 * 
 *
 * ```typescript
 * connection.fetchUserInfoById('user1') | fetchUserInfoById(['user1', 'user2'])
 * ```
 */
function fetchUserInfoById(
	this: any,
	/** The array of IDs of users or user ID to query.  */
	/** ID */
	userId: UserId | UserId[],
	/** User properties to query. If the parameter is blank, query all. */
	/**  */
	properties?: IndexTypes.ConfigurableKey[] | IndexTypes.ConfigurableKey
): Promise<AsyncResult<IndexTypes.UpdateOwnUserInfoParams>> {
	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	const { orgName, appName, accessToken } = this.context;
	const configurableKey: IndexTypes.ConfigurableKey[] = [
		'nickname',
		'avatarurl',
		'mail',
		'phone',
		'gender',
		'sign',
		'birth',
		'ext',
	];
	let targets = [];
	let querykeys = [];
	if (typeof userId === 'string') {
		targets = [userId];
	} else if (Object.prototype.toString.call(userId) === '[object Array]') {
		targets = userId;
	} else {
		throw new Error('illegal params');
	}

	if (typeof properties === 'string') {
		querykeys = [properties];
	} else if (
		properties &&
		Object.prototype.toString.call(properties) === '[object Array]'
	) {
		querykeys = properties;
	} else {
		querykeys = configurableKey;
	}

	const opts = {
		url: `${this.apiUrl}/${orgName}/${appName}/metadata/user/get`,
		type: 'POST',
		data: JSON.stringify({
			targets: targets,
			properties: querykeys,
		}),
		dataType: 'json',
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
	};
	logger.debug('Call fetchUserInfoById', userId);
	return ajax.call(this, opts, OperationName.GET_USER_INFO);
}

