/**
 * Unidirectionally deletes historical messages from the server.
 *
 * ```typescript
 * connection.removeHistoryMessages({targetId: 'userId', chatType: 'singleChat', beforeTimeStamp: Date.now()})
 *
 * connection.removeHistoryMessages({targetId: 'userId', chatType: 'singleChat', messageIds: ['messageId']})
 * ```
 */
/**
 * 
 *
 * ```typescript
 * connection.removeHistoryMessages({targetId: 'userId', chatType: 'singleChat', beforeTimeStamp: Date.now()})
 *
 * connection.removeHistoryMessages({targetId: 'userId', chatType: 'singleChat', messageIds: ['messageId']})
 * ```
 */
async function removeHistoryMessages(
	this: any,
	options: {
		/** The ID of the peer user , group ID or chatRoom ID. */
		/**  IDIDID */
		targetId: string;
		/**
		 * The conversation type:
		 * - `singleChat`: one-to-one chat;
		 * - `groupChat`: group chat.
		 * - `chatRoom`: chat room.
		 */
		/**
		 * 
		 * - `singleChat`
		 * - `groupChat`
		 * - `chatRoom`
		 */
		chatType: ChatType;
		/** The ID list of messages to be deleted. A maximum of 20 message IDs can be passed in. */
		/**  ID  20  ID */
		messageIds?: Array<string>;
		/** The starting timestamp for message deletion. Messages with the timestamp before the specified one will be deleted. */
		/**  */
		beforeTimeStamp?: number;
	}
): Promise<void> {
	if (typeof options.targetId !== 'string' || options.targetId === '') {
		throw Error('"Invalid parameter targetId": ' + options.targetId);
	}

	if (!['singleChat', 'groupChat', 'chatRoom'].includes(options.chatType)) {
		throw Error('"Invalid parameter chatType": ' + options.chatType);
	}

	if (
		options.beforeTimeStamp &&
		(typeof options.beforeTimeStamp !== 'number' ||
			options.beforeTimeStamp < 0 ||
			options.beforeTimeStamp?.toString().length > 18)
	) {
		throw Error(
			'"Invalid parameter beforeTimeStamp": ' + options.beforeTimeStamp
		);
	}

	if (
		options.messageIds &&
		!(
			Array.isArray(options.messageIds) &&
			options.messageIds.length > 0 &&
			options.messageIds.length <= 20
		)
	) {
		throw Error('"Invalid parameter messageIds": ' + options.messageIds);
	}
	if (!options.messageIds && !options.beforeTimeStamp) {
		throw Error('messageIds or beforeTimeStamp field is required.');
	}

	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}

	const { orgName, appName, userId } = this.context;
	const deleteType = options.chatType === 'singleChat' ? 'userId' : 'groupId';
	const chatTypeApi = options.chatType === 'singleChat' ? 'chat' : 'group';

	const url = options.messageIds
		? `${this.apiUrl}/${orgName}/${appName}/sdk/message/roaming/${chatTypeApi}/user/${userId}?${deleteType}=${options.targetId}&msgIdList=${options.messageIds}&resource=${this.clientResource}`
		: `${this.apiUrl}/${orgName}/${appName}/sdk/message/roaming/${chatTypeApi}/user/${userId}/time?${deleteType}=${options.targetId}&delTime=${options.beforeTimeStamp}&&resource=${this.clientResource}`;

	const opts = {
		url: url,
		dataType: 'json',
		type: 'DELETE',
		headers: {
			Authorization: 'Bearer ' + this.token,
			'Content-Type': 'application/json',
		},
	};
	logger.debug('Call removeHistoryMessages', options);
	await ajax.call(this, opts);
}
/**
 * Gets the list of conversations from the server with pagination.
 *
 * The SDK returns the list of conversations in the reverse chronological order of their active time (the timestamp of the last message).
 *
 * If there is no message in the conversation, the SDK retrieves the list of conversations in the reverse chronological order of their creation time.
 *
 * ```typescript
 * connection.getServerConversations({pageSize:50, cursor: ''})
 * ```
 */

/**
 * 
 *
 * SDK 
 *
 *  SDK 
 *
 * ```typescript
 * connection.getServerConversations({pageSize:50, cursor: ''})
 * ```
 */
function getServerConversations(
	this: Connection,
	params: {
		/** The number of conversations that you expect to get on each page. The value range is [1,50] and the default value is `20`. */
		/**  [1,50] `20`*/
		pageSize?: number;
		/** The position from which to start getting data. If you set `cursor` to an empty string (''), the SDK retrieves conversations from the latest active one.*/
		/** ''SDK*/
		cursor?: string;
	}
): Promise<AsyncResult<IndexTypes.ServerConversations>> {
	if (!checkStatus.call(this)) {
		const error = ErrorEvent.create({
			type: Code.REST_PARAMS_STATUS,
			message: 'appkey or token error',
		});
		return Promise.reject(error);
	}
	if (params?.pageSize && typeof params.pageSize !== 'number') {
		throw Error(`Invalid parameter pageSize: ${params.pageSize}`);
	}
	if (params?.cursor && typeof params.cursor !== 'string') {
		throw Error(`Invalid parameter cursor: ${params.cursor}`);
	}
	const { orgName, appName, accessToken } = this.context;
	const url = `${this.apiUrl}/${orgName}/${appName}/sdk/user/${this.user}/user_channels/list?`;
	const queryData = {
		limit: params?.pageSize || 20,
		cursor: params?.cursor || '',
		need_mark: true,
	};
	const opts = {
		url: url,
		type: 'GET',
		dataType: 'json',
		data: queryData,
		headers: {
			Authorization: 'Bearer ' + accessToken,
			'Content-Type': 'application/json',
		},
	};
	logger.debug('Call getServerConversations', params);

	return new Promise((resolve, reject) => {
		ajax.call(this, opts, OperationName.GET_SESSION_LIST)
			.then(async (res) => {
				const dt = formatConversations.call(this, res);

				if (this._localCache) {
					const storePromises = dt.data.conversations.map(
						async (cvs) => {
							await this._localCache?.getInstance()?.storeMessage(
								//@ts-ignore
								cvs.lastMessage,
								MessageStatus.SUCCESS,
								true
							);

							await this._localCache
								?.getInstance()
								?.updateLocalConversation(
									getSessionId({
										conversationId: cvs.conversationId,
										conversationType: cvs.conversationType,
									}),
									{
										unReadCount: cvs.unReadCount,
									}
								);
						}
					);

					await Promise.all(storePromises);
				}

				resolve(dt);
			})
			.catch((e) => {
				reject(e);
			});
	});
}

function formatConversations(this: Connection, res: any) {
	const { cursor, channel_infos } = res.data;
	const list: Array<IndexTypes.ConversationItem> = [];
	channel_infos?.forEach((item: any) => {
		let lastMessage = null;
		if (item?.meta && JSON.stringify(item.meta) !== '{}') {
			item.meta.payload = JSON.parse(item.meta.payload);
			lastMessage = formatMessage.call(this, item.meta);
			if (
				lastMessage.type !== 'delivery' &&
				lastMessage.type !== 'read' &&
				lastMessage.type !== 'channel'
			) {
				lastMessage.chatType = CHAT_TYPE_MAP[
					lastMessage.chatType
				] as ChatType;
			}
		}
		const conversationItem: IndexTypes.ConversationItem = {
			conversationId: item.session_to,
			conversationType:
				item.session_type === 'chat' ? 'singleChat' : 'groupChat',
			isPinned: item.is_top,
			pinnedTime: item.is_top ? item.update_top_status_time : 0,
			unReadCount: item.unread_num,
			lastMessage,
		};
		if (item.marks) {
			conversationItem.marks = (
				item.marks as Array<keyof typeof IndexTypes.MarkType>
			).map((mark) => {
				return IndexTypes.MarkType[mark];
			});
		}
		list.push(conversationItem);
	});
	const data: IndexTypes.ServerConversations = {
		conversations: list,
		cursor: cursor || '',
	};
	return {
		type: res.type,
		data,
	};
}
